@smartphones: ~"only screen and (max-width: 650px)";
@tablets: ~"only screen and (min-width: 650px) and (max-width: 959px)";



html, body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
}



#blog {
  background: url('../images/backgrounds/blog.jpg');
  background-size: cover;
  background-color: #fff;
  flex: 1;
  // height: 100vh;
  padding: 40px 75px 25px 50px;
  display: flex;
  flex-direction: column;



  .left-side {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-between;
  }

  .content {
    height: 100%;
    display: flex;
    // flex-direction: column;
    // padding-top: 10vh;
    & > * {
      // flex: 1
    }

    .main-content {
      display: flex;
      flex-direction: column;
      align-items: stretch;
      flex: 1;
      padding: 120px 10px 10px 0;
      
      & > * {
        // display: flex;
        // flex: 1
      }

      .navigation-horizontal {
        display: flex;
        justify-content: flex-end;
        align-items: baseline;
        position: relative;
        right: -67px;
        z-index: 3;

        ul.numbers {
          display: flex;
          list-style: none;
          justify-content: flex-end;
          align-items: center;
          flex: 1;

          li {
            padding: 10px;
            // flex:.2;
            transform: rotate(270deg);
            a {
              text-decoration: none;
              color: #1d9dbd;
              font-family: 'PFDinTextPro-Regular';

              &:hover, &.active {
                color: #1d9dbd;
              }
            }
          }

          li.leading-line {
            width: 140px;
            transform: rotate(0deg);

            &:before {
              content: ' ';
              background: #cbdbe0;
              height: 1px;
              width: 100px;
              display: block;
            }
          }
        }

        ul.arrows {
          display: flex;
          list-style: none;
          flex-direction: column;
          justify-content: center;
          align-items: center;

          li {
            background: #249fbe;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #fff;

            &.forward {
              width: 65px;
              height: 65px;
            }

            &.back {
              width: 35px;
              height: 35px;
            }

            &.divider {
              flex: 1 1 10px;
              background: none;
            }

            &:not(.divider):hover > i {
              animation: arrow-shake .6s 1;
            }

            @keyframes arrow-shake {


            0% {
              -webkit-transform: translate3d(-1.5px, 0, 0);
              transform: translate3d(-1.5px, 0, 0);
            }


            50% {
              -webkit-transform: translate3d(1.5px, 0, 0);
              transform: translate3d(1.5px, 0, 0);
            }

            100% {
              -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0);
            }
          }
          }
        }
      }

      .divider {
        flex: 1;
      }

      .heading {
        color: #249fbe;
        font-size: 80.5px;
        text-transform: uppercase;
        font-family: 'Brandon Black';
        display: flex;
        flex-direction:column;
        align-items:flex-start;
        flex: 2;
        align-self: flex-end;
        .row {
          display:flex;
          flex-direction:row;
          width:50%;

          .box {
            flex: 1;

            &.big-flex {
              flex: 3;
            }

            &.text {
              flex: 10 0 auto;
            }

            &.align-right {
              text-align:right;
            }
          }
        }
      }

      .content {
        display: flex;
        justify-content: space-around;
        flex-direction: column;
        align-items: center;
        overflow: auto;

        article {
          display: flex;
          width: 80%;
          align-items: flex-start;
          flex-shrink: 0;
          // .image {
          //   img {

          //   }
          // }

          .content {
            display: flex;
            margin-left: 130px;
            
            h4 {
              color: #009ec0;
              font-family: 'Brandon Bold';
              font-size: 34px;
              margin: 0;
              align-self: flex-start;
              text-transform: uppercase;
            }

            p {
              font-family: 'Proxima Nova';
              color: #525a5d;
            }

            .footer {
              display: flex;
              font-family: 'Proxima Nova';
              font-size: 17px;
              flex: 1;
              justify-content: space-between;
              width: 200px;
              align-self: baseline;

              .date {
                color: #b1ccd2;
              }

              a {
                color: #ff7d00;

                &:hover {
                  color: #1f1f1f;
                }
              }
            }
          }
        }
      }
    }
  }
}