@smartphones: ~"only screen and (max-width: 650px)";
@tablets: ~"only screen and (min-width: 650px) and (max-width: 959px)";



html, body {
	height: 100%;
}

body {
  display: flex;
  flex-direction: column;
}


#contact {
  background: url('../images/backgrounds/contact.jpg');
  background-size: cover;
  background-color: #eff8f9;
  flex: 1;
  // height: 100vh;
  padding: 40px 75px 25px 50px;
  display: flex;
  flex-direction: column;



  .left-side {
		display: flex;
		flex: 1;
		flex-direction: column;
    justify-content: space-between;
  }


  .content {
  	height: 100%;
  	display: flex;
  	// flex-direction: column;
  	// padding-top: 10vh;
  	& > * {
  		// flex: 1
  	}

  	.main-content {
  		display: flex;
	  	flex-direction: column;
			align-items: stretch;
			flex: 1;
  		& > * {
  			// display: flex;
	  		// flex: 1
	  	}

	  	.navigation-horizontal {
			  display: flex;
			  justify-content: flex-end;
				align-items: baseline;
				position: relative;
				right: -67px;
				z-index: 3;

	  		ul.numbers {
	  			display: flex;
    			list-style: none;
    			justify-content: flex-end;
					align-items: center;
					flex: 1;

	  			li {
	  				padding: 10px;
	  				// flex:.2;
	  				transform: rotate(270deg);
	  				a {
	  					text-decoration: none;
	  					color: #1d9dbd;
	  					font-family: 'PFDinTextPro-Regular';

	  					&:hover, &.active {
	  						color: #1d9dbd;
	  					}
	  				}
	  			}

	  			li.leading-line {
	  				width: 140px;
	  				transform: rotate(0deg);

	  				&:before {
	  					content: ' ';
	  					background: #cbdbe0;
							height: 1px;
							width: 100px;
							display: block;
	  				}
	  			}
	  		}

	  		ul.arrows {
	  			display: flex;
	  			list-style: none;
				  flex-direction: column;
				  justify-content: center;
				  align-items: center;

				  li {
				  	background: #249fbe;
						border-radius: 50%;
						display: flex;
						align-items: center;
						justify-content: center;
						color: #fff;

						&.forward {
							width: 65px;
							height: 65px;
		  			}

		  			&.back {
							width: 35px;
							height: 35px;
		  			}

		  			&.divider {
							flex: 1 1 10px;
							background: none;
		  			}

		  			&:not(.divider):hover > i {
      				animation: arrow-shake .6s 1;
		  			}

		  			@keyframes arrow-shake {


					  0% {
					    -webkit-transform: translate3d(-1.5px, 0, 0);
					    transform: translate3d(-1.5px, 0, 0);
					  }


					  50% {
					    -webkit-transform: translate3d(1.5px, 0, 0);
					    transform: translate3d(1.5px, 0, 0);
					  }

					  100% {
					    -webkit-transform: translate3d(0, 0, 0);
					    transform: translate3d(0, 0, 0);
					  }
					}
				  }
	  		}
	  	}

  		.divider {
  			flex: 1;
  		}

  		.heading {
  			color: #249fbe;
				font-size: 80.5px;
				text-transform: uppercase;
				font-family: 'Brandon Black';
			  display: flex;
			  flex-direction:column;
			  align-items:flex-end;
		    flex: 2;
  			align-self: flex-end;
			  .row {
			    display:flex;
			    flex-direction:row;
			    width:50%;

			    .box {
			      flex: 1;

			      &.big-flex {
			      	flex: 3;
			      }

			      &.text {
			        flex: 10 0 auto;
			      }

			      &.align-right {
			        text-align:right;
			      }
			    }
			  }
			}

			.content {
				display: flex;

        & > * {
          flex: 1;
        }
				.left-side {

				}

				.right-side {
          .contact-list {
            display: flex;
            flex-direction: column;

            li {
              // height: 50px;
              margin: 5px 0;
              display: flex;
              align-items: center;
              
              i.fa {
                color: #303030;
                border: 1px solid #e5e5e5;
                background: #fff;
                border-radius: 50%;
                padding: 8px;
              }

              a {
                margin-left: 30px;
                font-family: 'Proxima Nova';
                color: #009ec0;
                font-style: 24px;
                text-transform: uppercase;
                text-decoration: none;
                font-weight: bold;
              }            
            }
          }

          .qr {
            margin-top: 50px;
          }
				}
			}

  		
  	}
  }
}

  footer.footer {
        display: flex;
        align-items: center;

        .copyright {
          flex: 3;
          font-family: 'Proxima Nova';
          font-size: 14px;
          color: #9db7c1;
        }

        .copyright-mobile {
          display: none;
        }

        .social {
          flex: 1;
          display: flex;
          align-items: center;

          .box {
            margin: 8px;

            i {
              color: #1d9dbd;
              font-size: 15px;
            }
          }

        }
        .cta {
          padding-left: 40px;
          font-family: 'Brandon Bold';
          font-size: 14px;

          a {
            text-decoration: none;
            color: #1d9dbd;
          }
        }

        ul.mobile-bottom-navigation {
          display: none;
        }
      }